import React from "react";
import { Box, Link, makeStyles } from "@material-ui/core";
import { motion } from "framer-motion";
import nohesiclub from "../assets/images/web3/nohesiclub.png";
import metagaia from "../assets/images/web3/metagaia.png";
import banksea from "../assets/images/web3/banksea.png";
import endofday from "../assets/images/web3/endofday.png";
import ksoid from "../assets/images/web3/ksoid.png";

const icons = {
    nohesiclub: nohesiclub,
    metagaia: metagaia,
    banksea: banksea,
    endofday: endofday,
    ksoid: ksoid,
}

const IconBtn = ({ icon: Icon, href, fontSize, img, ...rest }) => {
    const classes = useStyles({ fontSize, img });
    return (
        <Box
            display="inline"
            {...rest}
            component={motion.div}
            whileHover={{ scale: 1.1 }}
        >
            <Link href={href} target="_blank">
                {img ?
                    <img src={icons[img]} alt={img} className={classes.icon} /> :
                    <Icon className={classes.icon} />
                }
            </Link>
        </Box>
    );
};

const useStyles = makeStyles((theme) => ({
    icon: {
        color: theme.palette.text.secondary,
        transition: "0.1s",
        cursor: "pointer",
        margin: props => props.img ? `8px` : "",
        width: props => props.fontSize ? `${props.fontSize}px` : "32px",
        fontSize: props => props.fontSize ? `${props.fontSize}px` : "32px",
        "&:hover": {
            color: theme.palette.text.primary
        }
    },
}));

export default IconBtn;
